import React,{useState} from 'react'
import Header from '../component/shared/header';
import Footer from '../component/shared/footer';
import ScrollToTop from '../component/home/scrollToTop';
import Heading from '../component/shared/heading';
import {contactUs} from '../blockchain/lottery';
import {useAlert} from 'react-alert'
const ContactUs=(props)=>{
    const [name,setName]=useState("")
    const [email,setEmail]=useState("")
    const [phoneno,setPhone]=useState(0)
    const [message,setMessage]=useState("")
    const alert=useAlert()
    const submitInfo=async()=>{
      const response=await contactUs(name,email,phoneno,message)
      if(response.status==200)
      {
        setName("")
        setEmail("")
        setPhone(0)
        setMessage("")
      }else{
        alert.error(<p style={{color:"white"}}>{response.message}</p>)
      }
    }
    return(
        <div style={{marginBottom:20}}>
        {!(props.from==="home")&&<Header/>}
        {!(props.from==="home")&&<Heading tittle="Contact us"/>}
    <section className="contact-section">
    <div className="container">
      <div className="row">
        <div className="col-lg-12">
          <div className="contact-wrapper">
            <div className="contact-area d-flex">
              <div className="contact-form-area">
                <h3 className="title">Get In Touch</h3>
                <p>If you have any questions or queries our helpful support team will be more than happy to assist.</p>
                <form className="contact-form">
                  <div className="form-grp">
                    <input type="text" name="contact_name" onChange={(e)=>{
                      setName(e.target.value)
                    }} value={name} id="contact_name" placeholder="Full Name"/>
                  </div>
                  <div className="form-grp">
                    <input type="email" name="contact_email" onChange={(e)=>{
                      setEmail(e.target.value)
                    }} value={email} id="contact_email" placeholder="Email Address"/>
                  </div>
                  <div className="form-grp">
                    <input type="tel" name="contact_phone" onChange={(e)=>{
                      setPhone(e.target.value)
                    }} value={phoneno} id="contact_phone" placeholder="Phone No"/>
                  </div>
                  <div className="form-grp">
                    <textarea name="contact_message" onChange={(e)=>{
                      setMessage(e.target.value)
                    }} value={message} id="contact_message" placeholder="Message"></textarea>
                  </div>
                  <div className="form-grp">
                    <input className="submit-btn" onClick={()=>{
                      submitInfo()
                    }} type="button" value="sent message"/>
                  </div>
                </form>
              </div>
              <div className="address-area has_bg_image">
                <div className="address-area-header">
                  <h3 className="title">We Are Available</h3>
                  <span>24 Hours A Day, 365 Days A Year</span>
                </div>
                <div className="contact-address">
                  <h3 className="title">Contact Us</h3>
                  <ul>
                    <li>
                      <div className="icon"><i className="fa fa-phone"></i></div>
                      <div className="content">
                        <p>8583915287</p>
                        <p>9051536816</p>
                        <p>8777702870</p>
                        <p>8145022056</p>

                      </div>
                    </li>
                    <li>
                      <div className="icon"><i className="fa fa-envelope-o"></i></div>
                      <div className="content">
                        <p>debdut.saha.1@gmail.com</p>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="contact-wrapper-footer">
              <span>Please see our Frequent Asked Questions (FAQ) page to read more information:</span>
              <a href="#0" className="cmn-btn">check FAQs</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
  <div style={{marginTop:200}}>
  <Footer/>
  </div>
</div>
    );
}
export default ContactUs;