import React,{useState,useEffect} from 'react'
import {Card,CardBody,CardText,CardImg,Button,CardTitle,CardSubtitle} from 'reactstrap'
import {getWeb3InstanceLottery} from '../../contracts/Lottery'
import {getWeb3Instance} from '../../contracts/AllLottery'
import { useAlert } from 'react-alert'
import Alert from "../shared/alert"
import {withRouter} from 'react-router-dom'
import web3 from '../../web3'
import {removeLottery,drawLottery} from '../../blockchain/lottery'

const AllDetailsLottery=(props)=>{
    const [details,setDetails]=useState({})
    const [isLoading,setLoading]=useState(true)
    const [error,setError]=useState(null)
    const [numplayer,setNumPlayer]=useState(0)
    const [isManager,setIsManager]=useState(false)
    const [balance,setBalance]=useState(0)
    const [alreadyInLottery,setAlreadyInLottery]=useState(false)
    const [isDone,setIsDone]=useState(false)

    const alert=useAlert()
    useEffect(()=>{
        findDetails()
    },[])

    const onDrawLottery=async()=>{
        const response=await drawLottery(props.address)
        if(response.status==200)
        {
            alert.success(<p>The winner of the lottery is: {response.message}</p>)
        }else{
            alert.error(<p style={{color:"white"}}>{response.message}</p>)
        }
    }
    const findDetails=async()=>{
        try{
        let accounts=await web3.eth.getAccounts();        
        const instance=await getWeb3InstanceLottery(props.address)
        const info=await instance.methods.getLotteryInfo().call()
        const manager=await instance.methods.getManager().call()
        const players=await instance.methods.Allplayers().call()
        let balance=await instance.methods.getBalance().call()
        let isdone=await instance.methods.isDone().call()
        for(let i=0;i<players.length;i++)
        {
            if(accounts[0]==players[i])
            {
                setAlreadyInLottery(true)
                break
            }
        }
        const date=new Date(Number(info[2]))
        balance=web3.utils.fromWei(balance,'ether')
        const lotteryDetails={
            name:info[0],
            image:info[1],
            date:String(date),
            manager
        }
        if(accounts[0]==manager)
        {
            setIsManager(true)
        }
        setIsDone(isdone)
        setBalance(balance)
        setDetails(lotteryDetails)
        setNumPlayer(players.length)
        setLoading(false)
    }catch(err)
    {
        alert.error(<Alert text="Please provide valid address"/>)
        setError("Please provide valid address")
        setLoading(false)
    }
    }
    const onRemoveLottery=async()=>{
        const res=await removeLottery(props.address,details.name)
        if(res.includes("Successfully"))
        {
            alert.success(<p style={{color:"white"}}>{res}</p>)
            props.history.push("/alllotteries")
        }else{
            alert.error(<p style={{color:"white"}}>{res}</p>)
        }
    }
    if(isLoading)
    {
        return(
        <div className="container">
            <h2>Loading...</h2>
        </div>
        )
    }else{
        return(
            <div className="container" >
                {error==null?<Card>
                    <div style={{width:"100%",display:"flex",justifyContent:'center',marginTop:10}}>
                        <img src={details.image} width={100} height={100} style={{alignSelf:'center'}}/>
                    </div>
                    <CardBody>
                        <CardTitle><h3><u>{details.name}</u></h3></CardTitle><br/>
                        <CardSubtitle>The lottery is managed by <strong>{details.manager}</strong></CardSubtitle>
                        <br/>
                        <CardText>Participated already <strong>{numplayer} no. of players</strong></CardText>
                        <CardText>The wining prize for this lottery is: <strong>{balance} ether</strong></CardText>
                        <CardText>Date of Lottery is: <strong>{details.date}</strong></CardText>
                        {(!isManager && !alreadyInLottery && !isDone)&&<Button color="warning" onClick={()=>{
                            props.history.push(`/enterToLottery/${props.address}`)
                        }}>Enter into lottery</Button>}
                        {(isManager && !isDone)&&<Button color="outline-danger" onClick={onDrawLottery}>Draw Lottery</Button>}
                        <br/>
                        <br/>
                        {isManager && <Button color="danger" onClick={onRemoveLottery}>Remove Lottery</Button>}
                    </CardBody>
                </Card>:<h3>{error}</h3>}
            </div>
        );
    }
}
export default withRouter(AllDetailsLottery);