import web3 from '../web3';
import Axios from 'axios';
import url from '../url';

const address='0x07e781c35585eeb547dbA55Ff90A05e2b57D7467'
const fetchAbi=async()=>{
    let res=await Axios.get(url+"abi/ContactUs")
    if(res.status===200)
    {
        return res.data.abi
    }else{
        return null
    }
}
export const getWeb3InstanceContact=async()=>{
    const abi=await fetchAbi();
    if(abi!==null)
    return new web3.eth.Contract(JSON.parse(abi),address);
    else
    return null
}



