import React,{useState} from 'react'
import {InputGroup,Input,Label, Button} from 'reactstrap'
import { fileUpload } from '../../actions';
import { useAlert } from 'react-alert'
import Alert from "../shared/alert"
import {deployContract} from '../../blockchain/lottery'
const Form=()=>{
    const [name,setName]=useState("")
    const [file,setFile]=useState({})
    const [date,setDate]=useState("")
    const [disabled,setDisabled]=useState(false)
    const alert=useAlert()
    const fileUploadForm=(e)=>{
        const file=e.target.files[0]
        setFile(file)
    }

    const submit=async()=>{
        setDisabled(true)
        const resfile=await fileUpload(file)
        if(resfile!=null)
        {
            const image=resfile.location
            if(date && image && name)
            {
                const data=await deployContract(image,name,date)
                if(data==="Deployed successfully")
                {
                    alert.success(<Alert text="Successfully created the lottery"/>)   
                }else{
                    alert.error(<Alert text={data}/>)
                }
            }else{
                alert.error(<Alert text="Something went wrong"/>)
            }
        }else{
            alert.error(<Alert text="Something went wrong"/>)
        }
        setDisabled(false)
    }
    const onChangeDate=(e)=>{
        let date=new Date(e.target.value)
        setDate(date.getTime())
    }
    return(
    <div className="container">
         <div className="row">
            <div className="col-lg-8" style={{marginBottom:20}}>
                <div>
                    <Label style={{color:"#03a1fc",paddingBottom:5,paddingTop:5}}><b>Lottery Name</b></Label>
                    <InputGroup>
                        <Input type="text" disabled={disabled} onBlur={(e)=>{
                            setName(e.target.value)
                        }}/>
                    </InputGroup>
                </div>
                <div>
                    <Label style={{color:"#03a1fc",paddingBottom:5,paddingTop:5}}><b>Lottery Image</b></Label>
                    <InputGroup>
                        <Input type="file" onChange={fileUploadForm} disabled={disabled}/>
                    </InputGroup>
                </div>
                <div>
                    <Label style={{color:"#03a1fc",paddingBottom:5,paddingTop:5}}><b>Date</b></Label>
                    <InputGroup>
                        <Input type="date" onChange={onChangeDate} disabled={disabled}/>
                    </InputGroup>
                </div>
                <div style={{marginTop:20}}>
                    <Button color="warning" disabled={disabled} onClick={submit}>Submit</Button>
                </div>
            </div>
        </div>
    </div>
    );
}
export default Form