import Axios from 'axios'
import url from '../url'
export const NewLottery=async(image,name)=>{
    let data={
        image,
        name
    }
    let response=await Axios.post(url+'deploy/Lottery',data).then(response=>{
        if(response.status==200)
        {
            return response.data;
        }
        return null;
    }).catch(err=>{
        return null
    })
    return response;
}

export const fileUpload=async(file)=>{
    const fd=new FormData()
    fd.append('file',file,file.name)
    return await Axios.post(url+"fileupload",fd).then(res=>{
        if(res.status===200)
        {
            return res.data
        }else{
            return null
        }
    }).catch(err=>{
        console.log(err.message)
        return null
    })
}