import React from 'react'

const Winner=(props)=>{
  const {data}=props
    return(
        <div className="col-lg-3 col-md-4 col-sm-6">
          <div className="winner-item m-bottom-30">
            <div className="thumb">
              {data.image && <img src={data.image} />}
            </div>
            <div className="content">
              <span className="game-name">Lottery Name: <strong>{data.lotteryname}</strong></span>
              <span className="name">Lottery Date: <strong>{data.lotterydate}</strong></span>
              <span className="name">Winner Name: <strong>{data.name}</strong></span>
              <span className="name">Winner Email: <strong>{data.email}</strong></span>
              <span className="name">Winner Email: <strong>{data.price}</strong></span>
            </div>
          </div>
        </div>
    );
}

export default Winner;