import React from 'react'
import Header from '../component/shared/header';
import Footer from '../component/shared/footer';
import ScrollToTop from '../component/home/scrollToTop';
import Heading from '../component/shared/heading';
import AllDetailsLottery from '../component/AllDetailsLottery/allDetailsLottery'
const DetailsLottery=(props)=>{
    return(
        <div>
            <Header/>
            <Heading tittle={`DetailsLottery of ${props.match.params.id}`}/>
            <AllDetailsLottery address={props.match.params.id}/>
            <br/>
            <Footer/>
            <ScrollToTop/>
        </div>
    );
}
export default DetailsLottery