import React from 'react'
import Header from '../component/shared/header';
import Footer from '../component/shared/footer';
import ScrollToTop from '../component/home/scrollToTop';
import Heading from '../component/shared/heading';
import UserRegistrationForm from '../component/entertolottery/userRegistrationForm';
const EnterLottery=(props)=>{
    return(
        <div className="container">
            <Header/>
            <Heading tittle={`Enter into this ${props.match.params.id} lottery`}/>
            <UserRegistrationForm address={props.match.params.id}/>
            <br/>
            <Footer/>
            <ScrollToTop/>
        </div>
    )
}
export default EnterLottery;