import React,{useState,useEffect} from 'react'
import {InputGroup,Input,Label,Button} from 'reactstrap'
import { fileUpload } from '../../actions';
import { useAlert } from 'react-alert'
import {setUserInfo} from '../../blockchain/lottery'

const UserRegistrationForm=(props)=>{
    const [update,setUpdate]=useState(false)
    const [name,setName]=useState("")
    const [image,setImage]=useState("")
    const [email,setEmail]=useState("")
    const [phonenumber,setPhoneNumber]=useState(0)
    const [localstorage,setLocalStorage]=useState({})
    const [file,setFile]=useState("")
    const alert=useAlert()
    useEffect(()=>{
        setLocalStorage(window.localStorage)
        let userdata=window.localStorage.getItem("lotteryuser")
        userdata=JSON.parse(userdata)
        if(userdata)
        {
            setUpdate(true)
            setName(userdata.name)
            setImage(userdata.image)
            setEmail(userdata.email)
            setPhoneNumber(userdata.phonenumber)
        }
    },[])
    const fileUploadForm=(e)=>{
        const file=e.target.files[0]
        setFile(file)
    }
    const onSubmit=async()=>{
        if(image==="" && file!=="")
        {
            const resfile=await fileUpload(file)
            const image=resfile.location
        }
            window.localStorage.setItem("lotteryuser",JSON.stringify({
                name,
                image,
                email,
                phonenumber
            }))
            console.log("Hiii")
            const response=await setUserInfo(name,image,email,phonenumber,props.address)
            if(response==="Successfully done")
            {
                alert.success(<p style={{color:'white'}}>{response}</p>)
            }else{
                alert.error(<p style={{color:'white'}}>{response}</p>)
            }
    }
    if(update)
    {
        return (
            <div className="container">
                <Button color="info" onClick={()=>{
                    setUpdate(false)
                }}>Update information</Button>
                <br/>
                <br/>
                <Button color="outline-warning" onClick={onSubmit}>enter details</Button>
            </div>
        );
    }
    return(
        <div className="container">
            <p>To enter to this lottery you have to give us your information.</p>
            <Label>Name</Label>
            <InputGroup>
                <Input onChange={(e)=>{
                    setName(e.target.value)
                }} value={name}/>
            </InputGroup>
            <br/>
            <Label>Profile Picture</Label>
            <InputGroup>
                {image!==""&&<img src={image} width={100} height={100}/>}
                <Input type="file" onBlur={fileUploadForm}/>
            </InputGroup>
            <br/>
            <Label>Email</Label>
            <InputGroup>
                <Input type="email" value={email} onChange={(e)=>{
                    setEmail(e.target.value)
                }}/>
            </InputGroup>
            <br/>
            <Label>Phone Number</Label>
            <InputGroup>
                <Input type="number" value={phonenumber} onChange={(e)=>{
                    setPhoneNumber(e.target.value)
                }}/>
            </InputGroup>
            <br/>
            <Button color="outline-warning" onClick={onSubmit}>enter details</Button>
            <br/>
        </div>
    );
}
export default UserRegistrationForm;