import web3 from '../web3'
import {getWeb3Instance} from '../contracts/AllLottery';
import {fetchAbiandByte} from '../contracts/Lottery'
import {getWeb3InstanceLottery} from '../contracts/Lottery';
import {getWeb3InstanceContact} from '../contracts/contact';
const NewLottery=async(image,name,date)=>
{
    try{
    let accounts=await web3.eth.getAccounts();
    console.log('Attempting to deploy from account',accounts[0])
    const data=await fetchAbiandByte()
    const result = await new web3.eth.Contract(JSON.parse(data.abi))
     .deploy({data: '0x' + data.bytecode,arguments:[image,name,date]}) // add 0x bytecode
     .send({from: accounts[0]});
    return result.options.address;
    }catch(err)
    {
        console.error(err)
        return null
    }
}

export const deployContract=async(image,name,date)=>{
    try{
    const AllLottery=await getWeb3Instance()
    const address=await NewLottery(image,name,date)
    if(address!=null)
    {
        const userAddress=await web3.eth.getAccounts()
        await AllLottery.methods.newLottery(address).send({
            from:userAddress[0]
        })
        return "Deployed successfully"
    }
    else{
        return "Something went wrong deloying the lottery"
    }
}catch(err)
{
    return err.message
}
}

export const setUserInfo=async(name,image,email,phonenumber,address)=>{
    try{
        let accounts=await web3.eth.getAccounts();
        const instance=await getWeb3InstanceLottery(address)
        await instance.methods.setUser(name,image,email,phonenumber,accounts[0]).send({
            from:accounts[0]
        })
        await instance.methods.enter().send({
            from:accounts[0],
            value:web3.utils.toWei('0.02','ether')
        })
        return "Successfully done"
    }catch(err)
    {
        console.log(err)
        return err.message
    }
}

export const removeLottery=async(address,name)=>{
    let accounts=await web3.eth.getAccounts();        
    const instance=await getWeb3Instance()
    try{
    const val=await instance.methods.removeLottery(address).send({
        from:accounts[0]
    })
    return `Successfully removed lottery ${name}`
    }catch(err)
    {
        return err.message
    }
}

export const drawLottery=async(address)=>{
    let accounts=await web3.eth.getAccounts()
    const instance=await getWeb3InstanceLottery(address)
    try{
        await instance.methods.pickWinner().send({
            from:accounts[0]
        })
        const winneraddress=await instance.methods.getWinner().call()
        if(winneraddress)
        {
            return{
                status:200,
                message:winneraddress
            }
        }else{
            return {
                status:500,
                message:"Something went wrong"
            }
        }
    }catch(err)
    {
        return {
            status:500,
            message:err.message
        }
    }
}
export const fetchWinnersfromAllLottery=async()=>{
    try{
        const instance=await getWeb3Instance()
        const lotteries=await instance.methods.getLotteries().call()
        let detailWinner=[]
        for(let i=0;i<lotteries.length;i++)
        {
            let instanceLottery=await getWeb3InstanceLottery(lotteries[i])
            let winneraddress=await instanceLottery.methods.getWinner().call()
            let info=await instanceLottery.methods.getLotteryInfo().call()
            let isdone=await instanceLottery.methods.isDone().call()
            let pricewon=await instanceLottery.methods.balancewon().call()
            if(isdone)
            {
                let details=await instanceLottery.methods.getUserName(winneraddress).call()
                let detail={
                    address:winneraddress,
                    name:details[0],
                    image:details[1],
                    email:details[2],
                    lotteryname:info[0],
                    lotteryimage:info[1],
                    lotterydate:info[2],
                    price:pricewon
                }
                detailWinner.push(detail)
            }
        }
        return {
            status:200,
            data:detailWinner,
            message:"Successfully fetched all the data of winners"
        }
    }
    catch(err)
    {
        return {
            status:400,
            message:err.message
        }
    }
}

export const contactUs=async(name,email,phoneno,message)=>{
    try{
    const instance=await getWeb3InstanceContact()
    let accounts=await web3.eth.getAccounts();
    await instance.methods.setMessage(name,email,phoneno,message).send({
        from:accounts[0]
    })
    return {status:200,message:"Successfully registered"}
}catch(err)
{
    return {status:500,message:err.message}
}
}
