import web3 from '../web3';
import Axios from 'axios';
import url from '../url';

export const fetchAbiandByte=async()=>{
    let res=await Axios.get(url+"abi/Lottery")
    if(res.status===200)
    {
        return res.data
    }else{
        return null
    }
}
export const getWeb3InstanceLottery=async(address)=>{
    const res=await fetchAbiandByte();
    if(res!==null)
    return new web3.eth.Contract(JSON.parse(res.abi),address);
    else
    return null
}



