import React,{useState,useEffect} from 'react'
import {getWeb3Instance} from '../../contracts/AllLottery'
import {getWeb3InstanceLottery} from '../../contracts/Lottery'
import {Redirect} from 'react-router'
import {
  withRouter
} from 'react-router-dom'
const JackPot=(props)=>{

    const [alllotteries,setAllLotteries]=useState([])
    useEffect(()=>{
      fetchAllLotteries()
    },[])

    const fetchAllLotteries=async()=>{
      const instance=await getWeb3Instance()
      const arr=await instance.methods.getLotteries().call()
      let allLotteries=[]
      let len=arr.length
      if(arr.length>3 && props.from==="Index")
      {
        len=3
      }
      for(let i=0;i<len;i++)
      {
        let instanceLottery=await getWeb3InstanceLottery(arr[i])
        let value=await instanceLottery.methods.getLotteryInfo().call()
        let date=new Date(Number(value[2]))
        let data={
          address:arr[i],
          name:value[0],
          image:value[1],
          date:String(date)
        }
        allLotteries.push(data)
      }
      setAllLotteries(allLotteries)
    }
    const handlePlayNow=(address)=>{
      console.log(props)
      props.history.push(`/details/lottery/${address}`)
    }
    return(
        <section className="jackpot-section section-padding">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-lg-5">
            <div className="section-header text-center">
              <h2 className="section-title">Lottery Jackpots</h2>
              <p>Choose from the Powerball, Mega Millions, Lotto or Lucky Day Lotto and try for a chance to win a big cash prize</p>
            </div>
          </div>
        </div>
        <div className="row">
          {alllotteries.map((item,index)=>{
            return(
              <div className="col-lg-4 col-md-6" key={index}>
            <div className="jackpot-item text-center">
              <img src={item.image} />
              <span className="amount"></span>
              <h5 className="title">{item.name}</h5>
              <p className="next-draw-time">Next Draw : <span id="remainTime1">{item.date}</span></p>
              <a className="cmn-btn" style={{textDecoration:"None"}} onClick={()=>{
                handlePlayNow(item.address)
              }}>play now!</a>
            </div>
          </div>)
          })}
          <div className="col-lg-12 text-center" onClick={()=>{
            props.history.push('/allLotteries')
          }}>
            <a className="text-btn" >Show all lotteries</a>
          </div>
        </div>
      </div>
    </section>
    );
}
export default withRouter(JackPot);